import ArtworkGL from "./js/ArtworkGL";

export default {
  name: 'artwork',
  data () {
    return {
        artworkGL: 0,
        boxStatus: {
          x: 0,
          y: 0,          
        }        
    }
  },
  mounted () {
    if(!this.artworkGL) this.artworkGL = new ArtworkGL({
      $canvas: this.$refs.canvas
    });
    // this.artworkGL.init() 
  },
  
  methods: {
    makeBox(e){        
      if(this.artworkGL){
        this.boxStatus.x = e.offsetX
        this.boxStatus.y = e.offsetY  

        this.artworkGL.makeBox({
            x:this.boxStatus.x,
            y:this.boxStatus.y
        })
      }
    },    
  }
};
